import { Component, inject, OnDestroy, OnInit } from '@angular/core';
import { CustomTableComponent } from '../../../shared/custom-table/custom-table.component';
import { MatButton } from '@angular/material/button';
import { MatFormField } from '@angular/material/form-field';
import { MatIcon } from '@angular/material/icon';
import { MatInput } from '@angular/material/input';
import { MatTab, MatTabChangeEvent, MatTabContent, MatTabGroup } from '@angular/material/tabs';
import { CurrencyPipe, DatePipe, NgClass, NgIf } from '@angular/common';
import { map, Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { IButton } from '../../../shared/interfaces/buttonsInterfaces';
import { EstadoDetalleConsumo, EstadoDetalleConsumosId } from '../../../../core/enums/detalle-consumo';
import { EstadosDatosService } from '../../../../core/services/estados-datos.service';
import { DetalleConsumoService } from '../../../../core/services/detalle-consumo.service';
import { FuseAlertComponent } from '../../../../../@fuse/components/alert';
import { FuseConfirmationService } from '../../../../../@fuse/services/confirmation';
import { exportar } from '../../../../core/constant/dialogs';
import * as XLSX from 'xlsx';

@Component({
  selector: 'app-grid-consumos',
  standalone: true,
    imports: [
        CustomTableComponent,
        MatButton,
        MatFormField,
        MatIcon,
        MatInput,
        MatTab,
        MatTabContent,
        MatTabGroup,
        NgIf,
        FuseAlertComponent,
        NgClass,
    ],
    providers: [
        DatePipe,
        CurrencyPipe
    ],
  templateUrl: './grid-consumos.component.html',
  styleUrl: './grid-consumos.component.scss'
})
export class GridConsumosComponent implements OnInit, OnDestroy{
    public subcription$: Subscription;
    public selectedData: any;
    private datePipe = inject(DatePipe);
    private currencyPipe = inject(CurrencyPipe);
    private router = inject(Router);
    private estadoService: EstadosDatosService = inject(EstadosDatosService);
    private consumoService = inject(DetalleConsumoService);
    private selectedTab: any = EstadoDetalleConsumosId.EN_REVISION;
    public tabIndex ;
    public searchTerm: string = '';
    public fuseService = inject(FuseConfirmationService);
    data = [];

    columns = ['Fecha','Empresa', 'Número de factura', 'Trabajador', 'Valor factura', 'Cantidad cuotas', 'Estado'];
    columnPropertyMap = {
        'Fecha': 'fechaCreacion',
        'Empresa': 'nombreSubEmpresa',
        'Número de factura': 'numeroFactura',
        'Trabajador': 'nombreTrabajador',
        'Valor factura': 'montoConsumo',
        'Cantidad cuotas': 'cantidadCuotas',
        'Estado': 'nombreEstadoCredito',
    };

    buttons: IButton[] = [
        {
            label: 'View',
            icon: 'visibility',
            action: (element) => {
                console.log('Approve', element);
                this.selectedData = element;
                this.router.navigate(['pages/gestion-creditos/consumos/detalle', this.selectedData.id])

            }
        },
    ];

    private listenGrid() {
        const refreshData$ = this.estadoService.stateGridSolicitudes.asObservable();

        refreshData$.subscribe((states) => {
            if (states.state) {
                console.log('Si entro')
                console.log(states)
                this.selectedTab = states.tab == 0 ? EstadoDetalleConsumosId.EN_REVISION :
                                   states.tab == 1 ? EstadoDetalleConsumosId.APROBADAS :
                                   //states.tab == 2 ? EstadoDetalleConsumo.PAGADO :
                                   states.tab == 2 ? EstadoDetalleConsumosId.RECHAZADAS : EstadoDetalleConsumosId.EN_REVISION;
                this.tabIndex = states.tab;
                console.log(this.tabIndex)
                this.getDetalle(this.selectedTab);
            }
        })

    }

    tabChanged = (tabChangeEvent: MatTabChangeEvent): void => {
        console.log('tabChangeEvent => ', tabChangeEvent);
        console.log('index => ', tabChangeEvent.index);
        this.tabIndex = tabChangeEvent.index;
        console.log(this.tabIndex)
        this.selectedTab = tabChangeEvent.index == 0 ? EstadoDetalleConsumosId.EN_REVISION :
                           tabChangeEvent.index == 1 ? EstadoDetalleConsumosId.APROBADAS :
                           //tabChangeEvent.index == 2 ? EstadoDetalleConsumo.PAGADO :
                           tabChangeEvent.index == 2 ? EstadoDetalleConsumosId.RECHAZADAS : EstadoDetalleConsumosId.EN_REVISION;
        this.getDetalle(this.selectedTab);

    }

    onSearch(event: Event) {
        const target = event.target as HTMLInputElement;
        this.searchTerm = target.value.trim().toLowerCase();
    }

    getDetalle(param): void {

        this.subcription$ = this.consumoService.getDetalle(param).pipe(
            map((response) => {
                response.data.forEach((items) => {
                    items.fechaCreacion = this.datePipe.transform(items.fechaCreacion, 'dd/MM/yyyy');
                    items.montoConsumo = this.currencyPipe.transform(items.montoConsumo, 'USD', 'symbol', '1.2-2');
                    items.montoCuotas = this.currencyPipe.transform(items.montoCuotas, 'USD', 'symbol', '1.2-2');
                })
                return response;
            })
        ).subscribe((response) => {
            if (response) {
                this.data = response.data;
            }else {
                this.data = [];
            }
        }, error => {
            this.data = [];
        })
    }

    ngOnDestroy(): void {
        this.subcription$.unsubscribe();
    }

    ngOnInit(): void {
        this.getDetalle(this.selectedTab);
        this.listenGrid();
    }

    exportToExcel(data: any[]) {
        const dialog = this.fuseService.open({
            ...exportar
        });

        dialog.afterClosed().subscribe((response) => {
            if (response === 'confirmed') {
                // Create worksheet
                const worksheet = XLSX.utils.json_to_sheet(data);

                // Create workbook
                const workbook = XLSX.utils.book_new();
                XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');

                // Export file
                XLSX.writeFile(workbook, 'exported_data.xlsx');
            }
        })

    }

}
